ul.news {
  list-style: none;
  padding-inline-start: 0px;
  padding-bottom: 100px;
}

.news li {
  border-bottom: 1px solid rgba(42, 44, 50, 0.54);
  margin-top: 10px;
  transition: .3s ease;
}

.news li:hover {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.news p {
  margin: 0;
}

.news li button {
  cursor: pointer;
  border-style: none;
  background: none;
  text-align: left;
}
